.navbar {
  width: 100%;
  height: 80px;
  background-color: rgb(19, 19, 19);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar .heading {
  color: white;
  margin-left: 2rem;
  cursor: pointer;
}

.navbar .links {
  margin-right: 50px;
  display: flex;
  align-items: center;
}

.navbar .links a {
  text-decoration: none;
  color: white;
  margin-left: 20px;
  font-size: 25px;
}
